import { EventEmitter, ChangeDetectorRef, Component, ElementRef, Injector, Input, OnInit, Output, AfterViewInit } from '@angular/core';

@Component({
    selector: 'search-field-editor-bool',
    templateUrl: './search-editor-boolean.component.html'
})
export class SearchFieldEditorBooleanComponent implements OnInit, AfterViewInit {

    @Input() field: any;

    radioDataSource = [];
    selectedValue = undefined;

    @Output() valueChange = new EventEmitter();
    private _radioElements = [];

    constructor(private injector: Injector, private cd: ChangeDetectorRef, private el: ElementRef) { }

    ngOnInit() {
        if (this.field) {
            if (this.field.control) {
                this.radioDataSource = this.field.control.enumValues || [];
            }

            if (this.field.value) {
                this.selectedValue = this.field.value.value;
            }
        }
    }

    ngAfterViewInit(): void {
        if (this.selectedValue !== null && this.selectedValue !== undefined) {
            this._radioElements = Array.from(this.el.nativeElement.querySelectorAll('input'));
            const radioEl = this._radioElements.find(n => n.value == this.selectedValue);
            if (radioEl) {
                radioEl.checked = true;
            }
        }
    }

    onRadioClicked($event, value) {
        this.selectedValue = value;
        const radioInputRel = $event.target.querySelector('input');
        // radioInputRel && radioInputRel.setAttribute('checked', true);
        if (radioInputRel) {
            this.clearChecked();
            radioInputRel.checked = true;
            this.cd.detectChanges();
        }

        this.valueChange.emit({ field: this.field.code,  value: {value: this.selectedValue, single: true} });
    }

    private clearChecked() {
        if (!this._radioElements || !this._radioElements.length) {
            this._radioElements = Array.from(this.el.nativeElement.querySelectorAll('input'));
        }
        this._radioElements.forEach((r: any) => {
            // r.removeAttribute('checked');
            r.checked = false;
        });
    }
}