import { Injector, Pipe, PipeTransform } from '@angular/core';
import { SearchBoxComponent } from '../search-box.component';

@Pipe({
    name: 'searchwords'
})

export class SearchWordPipe implements PipeTransform {

    constructor(private injector: Injector,  private searchBoxRef: SearchBoxComponent,) {}

    transform(value: any, ...args: any[]): any {
        if (value !== undefined && value !== null) {
            const relationStr = this.searchBoxRef.localeSer.getValue('searchbox.relation.or');
            const arr = value.trim().split(' ').filter(n => n.trim());
            return arr.map((n, i) => {
                return n  +  (i === arr.length -1 ? '' : '<span class="text-muted" style="margin: 0 5px;"> '+ relationStr +' </span>');
            }).join('');
        }

        return '';
    }
}