import { AfterViewInit, ChangeDetectorRef, Component, ElementRef, EventEmitter, forwardRef, HostBinding, HostListener, 
    Injector, Input, OnChanges, OnDestroy, OnInit, Optional, Output, Renderer2, SimpleChanges, ViewChild, ViewEncapsulation } from "@angular/core";
import { OverLayHiddenService } from "@farris/ui-common";
import { Compare, FilterCondition, FilterRelation } from "@farris/ui-common/types";
import { DisplayText, SearchField, SearchFieldValue } from "../search-box.types";
import { flatten } from 'lodash-es';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from "@angular/forms";
import { BehaviorSubject, of, Subject } from "rxjs";
import { SearchBoxBase } from "../search-box-base";
import { DateTimeHelperService } from "@farris/ui-common/date";

export const SEARCH_BOX_VALUE_ACCESSOR: any = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => SearchFieldsComponent),
    multi: true
};

@Component({
    selector: 'farris-search-fields',
    templateUrl: 'search-fields.component.html',
    // styleUrls: [
    //     `../searchbox-panel.scss`
    // ],
    encapsulation: ViewEncapsulation.None,
    providers: [
        SEARCH_BOX_VALUE_ACCESSOR
    ]
})
export class SearchFieldsComponent extends SearchBoxBase implements OnInit, OnChanges, ControlValueAccessor, AfterViewInit, OnDestroy {
    @Input() fields: Array<SearchFieldValue> = [];
    @Input() maxSearchFields = 5;
    @Input() condition: Array<FilterCondition> = [];
    /** å¤å­æ®µæ¥è¯¢ */
    @Input() multi = true;
    
    @ViewChild('searchbox') container: ElementRef;
    @ViewChild('searchbox2') shadowBox: ElementRef;
    @ViewChild('tagContainer') tagContainerRef: ElementRef;

    @Output() conditionChange = new EventEmitter();
    @Output() clear = new EventEmitter();
    /** å¯ç¨æ¼æµ®ï¼false: åå®¹ä¼æé«å®¹å¨ï¼ trueï¼åºå®é«åº¦26pxï¼ç¹å»ææ»è¿æ¶ï¼æ¾ç¤ºå®æ´åå®¹ï¼å¤±å»ç¦ç¹åï¼è¿ååºå®ç¶æ */
    enableFloat = true;

    displayTextList: Array<DisplayText> = [];
    // filterConditions: Array<SearchFieldValue> = [];

    moreStatus$ = new BehaviorSubject(false);

    hideShadowbox = new Subject();

    get hasFilter() {
        return this.displayTextList && this.displayTextList.length ? this.displayTextList.filter((n: any) => n.hasFilter).length : false;
    }

    private showShadowBox = false;

    constructor(el: ElementRef, injector: Injector, @Optional() overLayService: OverLayHiddenService, cd: ChangeDetectorRef, private dateHelp: DateTimeHelperService) {
        super(el, injector, overLayService, cd);
    }

    ngOnInit() {

        this.initDislayText(!!this.fields.filter((n: any) => n.value).length);

        this.overLayService.registerMouseEvent(this.el.nativeElement, (e: any) => {
            if (this.shadowBox && this.showShadowBox) {
                if (this.shadowBox.nativeElement.contains(e.target)) {
                    return;
                }
                if (e.target.closest('.f-search-field-container')) {
                    return;
                }

                if (e.target.closest('.date-overlay-container') || e.target.className.indexOf('date-overlay-container') > -1) {
                    return;
                }

                this.toggleShadowBox(false);
                this.hideShadowbox.next(e);
            }
        });
    }

    ngAfterViewInit(): void {
        setTimeout(() => {
            this.moveShadowToBody();
        });

    }

    ngOnChanges(changes: SimpleChanges) {
       
    }

    ngOnDestroy() {
        if (this.overLayService) {
            this.overLayService = null;
        }

        if (this.shadowBox) {
            this.shadowBox.nativeElement.remove();
        }
    }


    onModelChange = (arr: any) => { };
    onModelTouched = (arr: any) => { };

    private filterChanged() {
        const conditions = this.fields.filter((n: any) => n.value).map((n: any) => {
            const { value, begin, end } = n.value;

            if (value != null && value !== undefined && n.control.single) {
                if (n.control.controltype === 'datetime') {
                    return [{
                        filterField: n.code,
                        value,
                        lbracket: '(',
                        rbracket:  '',
                        relation: FilterRelation.And,
                        compare: Compare.GreaterOrEqual
                    },
                    {
                        filterField: n.code,
                        value: this.dateHelp.formatTo(this.dateHelp.addDays(new Date(value), 1), n.control.dateFormat),
                        lbracket: '',
                        rbracket:  ')',
                        relation: FilterRelation.And,
                        compare: Compare.Less
                    }]
                } else if (n.control.controltype === 'enum' && Array.isArray(value)) {
                    return value.map((e, i) => {
                        return {
                            filterField: n.code,
                            value: e,
                            lbracket: i?'': '(',
                            rbracket:  i == value.length -1 ? ')' : '',
                            relation: i == value.length -1 ? FilterRelation.And: FilterRelation.Or,
                            compare: Compare.Equal
                        }
                    });
                }
                return {
                    filterField: n.code,
                    value: Array.isArray(value) ? value.join(',') : value,
                    lbracket: '',
                    rbracket:  '',
                    relation: FilterRelation.And,
                    compare: Array.isArray(value) ?  Compare.In: Compare.Equal
                }
            } else {
                if (begin !== '' && begin!=null && begin != undefined && end !=='' && end !=null && end != undefined) {
                    return [
                        {
                            filterField: n.code,
                            value: begin,
                            lbracket: '(',
                            rbracket:  '',
                            relation: FilterRelation.And,
                            compare: Compare.GreaterOrEqual
                        },
                        {
                            filterField: n.code,
                            value: end,
                            lbracket: '',
                            rbracket:  ')',
                            relation: FilterRelation.And,
                            compare: Compare.LessOrEqual
                        }
                    ]
                } else {
                    if (begin !== '' && begin!=null && begin != undefined) {
                        return {
                            filterField: n.code,
                            value: begin,
                            lbracket: '',
                            rbracket:  '',
                            relation: FilterRelation.And,
                            compare: Compare.GreaterOrEqual
                        }
                    }

                    if (end !=='' && end !=null && end != undefined) {
                        return {
                            filterField: n.code,
                            value: end,
                            lbracket: '',
                            rbracket:  '',
                            relation: FilterRelation.And,
                            compare: Compare.LessOrEqual
                        }
                    }
                }

                return null;
            }
        }).filter(n => n);

        let _conditions: Array<FilterCondition> = [];
        if (conditions && conditions.length) {
            _conditions = flatten(conditions)
            _conditions[_conditions.length - 1].relation = FilterRelation.Empty;
        }
        this.onModelChange(this.fields);
        this.onModelTouched(this.fields);
        this.conditionChange.emit(_conditions);
    }

    setConditions(condition: any) {
        const field: any = this.fields.find(n => n.code === condition.field);
        field.value = condition.value;
        field.control.single = condition.value.single;

        this.initDislayText();
        this.cd.detectChanges();
    }


    private initDislayText(emit = true) {
        this.displayTextList = this.fields.map((t: any) => {
            const n = {...t};
            n.dislayText = n.name;
            n.hasFilter = false;
            if (n.value) {
                const {value, begin, end} =  n.value;
                let val = value;

                if (t.control && t.control.enumValues && t.control.enumValues.length) {
                    if (!Array.isArray(val)) {
                        val = [val];
                    }
                    val = val.map(n => {
                        const d = t.control.enumValues.find(e => e.value === n);
                        if (d) {
                            return d.name;
                        }
                        return n;
                    }).join(',');
                } else {
                    if (begin !== null && begin !== undefined) {
                        val = begin;
                    }
                    if (end !== null && end !== undefined) {
                        if (val != null) {
                            val += '~' + end;
                        } else {
                            val = end;
                        }
                    }
                }
                n.dislayText += 'ï¼' + val;
                n.hasFilter = true;
            }
            return n;
        });

        this.setMoreStatus();
        
        if (emit) {
            this.filterChanged();
        }
    }

    remove(field: string) {
        const searchField = this.fields.find(n => n.code === field);
        if (searchField) {
            searchField.value = null;
            this.initDislayText();
        }
    }



    onRemoveItem($event, item) {
        if (item.value!== null && item.value != undefined && item.value !== '') {
            $event.stopPropagation();
            this.remove(item.code);
        }
    }

    clearConditions(emit = true) {
        this.fields.forEach(n => n.value = null);
        this.initDislayText(emit);
        if (emit) {
            this.clear.emit();
        }
        this.cd.detectChanges();
    }

    writeValue(obj: any): void {
        let arr = obj;
        if (!Array.isArray(obj)) {
            arr = [];
        }

        this.fields = arr;
        this.initDislayText();
    }

    registerOnChange(fn: any): void {
        this.onModelChange = fn;
    }

    registerOnTouched(fn: any): void {
        this.onModelTouched = fn;
    }

    setValue(arr: Array<SearchFieldValue>) {
        // this.filterConditions = arr;
        // this.initDislayText();
    }

    updateShadowBoxPosition({top, left, width} = {top: null, left: null, width: null}) {
        if (!top && !left && !width) {
            const e = this.el.nativeElement.getBoundingClientRect();
            top = e.top;
            left = e.left;
            width = e.width;
        }
        this.render.setStyle(this.shadowBox.nativeElement, 'left', left + 'px');
        this.render.setStyle(this.shadowBox.nativeElement, 'top', top + 'px');
        this.render.setStyle(this.shadowBox.nativeElement, 'width', width + 'px');

        this.render.setStyle(this.shadowBox.nativeElement, 'zIndex', this.commonUtils.getFloatingLayerIndex());
    }

    private moveShadowToBody() {
        if (!this.enableFloat) {
            return;
        }

        this.updateShadowBoxPosition()
        document.body.append(this.shadowBox.nativeElement);
    }

    toggleShadowBox(show = true, el = null) {
        if (!this.enableFloat) {
            return;
        }

        if (el && el.clientHeight <= 30) {
            return;
        }

        this.showShadowBox = show;
        if (show) {
            this.updateShadowBoxPosition();
            this.render.removeStyle(this.shadowBox.nativeElement, 'display');
            this.container.nativeElement.style.visibility = 'hidden';
            this.render.addClass(this.shadowBox.nativeElement, 'active');
        } else {
            this.container.nativeElement.style.visibility = 'visible';
            this.render.setStyle(this.shadowBox.nativeElement, 'display', 'none');
            this.render.removeClass(this.shadowBox.nativeElement, 'active');
        }
    }


    onResized($event) {
        if (this.enableFloat) {
            this.updateShadowBoxPosition($event);
            this.setMoreStatus();
        }
    }

    private setMoreStatus() {
        if (this.enableFloat) {
            setTimeout(() => {
                const r = this.tagContainerRef.nativeElement.clientHeight > 30;
                this.moreStatus$.next(r);
                this.cd.detectChanges();
            });
        }
    }
}