import { ChangeDetectorRef, Component, ElementRef, EventEmitter, HostBinding, HostListener, Injector, Input, OnInit, Output, ViewChild, ViewEncapsulation } from '@angular/core';
import { SearchField } from '../search-box.types';

@Component({
    selector: 'search-panel',
    templateUrl: './search-panel.component.html',
    encapsulation: ViewEncapsulation.None
})

export class SearchPanelComponent implements OnInit {

    @Input() fields: Array<SearchField> = [];
    @Input() maxItems = 5;
    @Input() searchKeyWord = '';

    @Output() itemClick = new EventEmitter();
    @Output() closepanel = new EventEmitter();
    @Output() mouseDown = new EventEmitter();

    @ViewChild('itemsContainer') itemsContainer: ElementRef<any>;

    activeIndex = -1;

    constructor(private injector: Injector, private cdr: ChangeDetectorRef) { }

    @HostListener('mousedown', ['$event'])
    onClick($event: MouseEvent) {
        $event.stopPropagation();
    }

    ngOnInit() { }

    loadData(items: any) {
        this.fields = items;

        if (!this.cdr['destroyed']) {
            this.cdr.detectChanges();
        }
    }

    onFieldItemClick($event, item) {
        $event.preventDefault();
        this.itemClick.emit({data: item, evnet: $event});
    }

    setActiveItem(index: number) {
        this.activeIndex = index;
        if (!this.cdr['destroyed']) {
            this.cdr.detectChanges();
        }
    }

    setValue(val) {
        this.searchKeyWord = val;
        this.cdr.detectChanges();
    }

    onClosePanel($event: MouseEvent) {
        $event.stopPropagation();
        this.closepanel.emit();
    }

    onMouseDown($event: MouseEvent) {
        $event.preventDefault();
        $event.stopPropagation();

        this.mouseDown.emit($event);
    }
}