import { AfterViewInit, ChangeDetectorRef, Component, ElementRef, EventEmitter, forwardRef, HostBinding, HostListener, Injector, Input, OnChanges, OnDestroy, OnInit, Optional, Output, Renderer2, SimpleChanges, ViewChild, ViewEncapsulation } from "@angular/core";
import { CommonUtils, OverLayHiddenService } from "@farris/ui-common";
import { InputGroupComponent } from "@farris/ui-input-group";
import { Compare, FilterCondition, FilterRelation } from "@farris/ui-common/types";
import { DisplayText, SearchField, SearchFieldValue } from "./search-box.types";
import { flatten } from 'lodash-es';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from "@angular/forms";
import { BehaviorSubject, of } from "rxjs";
import { SearchBoxBase } from "./search-box-base";


export const SEARCH_BOX_VALUE_ACCESSOR: any = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => SearchBoxComponent),
    multi: true
};
@Component({
    selector: "farris-search-box",
    templateUrl: './search-box.component.html',
    encapsulation: ViewEncapsulation.None,
    providers: [
        SEARCH_BOX_VALUE_ACCESSOR
    ]
})
export class SearchBoxComponent extends SearchBoxBase implements OnInit, OnChanges, ControlValueAccessor, AfterViewInit, OnDestroy {

    @Input() fields: Array<SearchField> = [];
    @Input() maxSearchFields = 5;
    @Input() condition: Array<FilterCondition> = [];

    /** å¯ç¨ ä»»æå­æ®µ */
    @Input() useAnyField = true;
    

    @ViewChild('searchbox') container: ElementRef;
    @ViewChild('tagInput') input: InputGroupComponent;


    @ViewChild('searchbox2') shadowBox: ElementRef;
    @ViewChild('tagInput2') input2: InputGroupComponent;


    @Output() conditionChange = new EventEmitter();
    @Output() clear = new EventEmitter();
    @Output() escHandler = new EventEmitter();
    /** å¯ç¨æ¼æµ®ï¼false: åå®¹ä¼æé«å®¹å¨ï¼ trueï¼åºå®é«åº¦26pxï¼ç¹å»ææ»è¿æ¶ï¼æ¾ç¤ºå®æ´åå®¹ï¼å¤±å»ç¦ç¹åï¼è¿ååºå®ç¶æ */
    enableFloat = true;

    displayTextList: Array<DisplayText> = [];
    filterConditions: Array<SearchFieldValue> = [];

    hiddenItemsCount$ = new BehaviorSubject(0);

    constructor(el: ElementRef, injector: Injector, @Optional() overLayService: OverLayHiddenService, cd: ChangeDetectorRef) {
       super(el, injector, overLayService, cd);
    }

    ngOnInit() {
    }

    ngAfterViewInit(): void {
        setTimeout(() => {
            this.moveShadowToBody();
        });

    }

    ngOnChanges(changes: SimpleChanges) {
    }

    ngOnDestroy() {
        if (this.overLayService) {
            this.overLayService.destory(this.shadowBox.nativeElement);
            this.overLayService = null;
        }

        if (this.shadowBox) {
            this.shadowBox.nativeElement.remove();
        }

        if (this['REMOVE_ITEM_TIMER']) {
            clearTimeout(this['REMOVE_ITEM_TIMER']);
            this['REMOVE_ITEM_TIMER'] = null;
        }
    }

    onBlur($event) {
        if (this.enableFloat) {
            if (!this.input2.value) {
                this.toggleShadowBox(false);
            }
        }
    }

    onInputClick($event) {
        if (this.enableFloat) {
            this.toggleShadowBox();
        }
    }


    onModelChange = (arr: any) => { };
    onModelTouched = (arr: any) => { };

    private filterChanged() {
        const conditions = this.filterConditions.filter(n => n.code !== '*').map(
            (n, i) => {
                if (n.value.trim().indexOf(' ') > -1) {
                    const searchKeys = Array.from(new Set(n.value.trim().split(' ').filter(n => n.trim())));
                    return searchKeys.map((s, j) => {
                        return {
                            filterField: n.code,
                            value: s,
                            lbracket: j == 0? '(':  '',
                            rbracket: j == searchKeys.length - 1 ? ')' : '',
                            relation: j == searchKeys.length - 1 ? 1 : 2,
                            compare: 6,
                        }
                    });
                }
                return {
                    filterField: n.code,
                    value: n.value,
                    lbracket: '',
                    rbracket: '',
                    relation: i == this.filterConditions.length -1 ? 0 : 1,
                    compare: 6,
                }
            }
        );

        let _conditions = [];
        _conditions = this.expandStarFieldToAllFields().concat(flatten(conditions));
        if (conditions && conditions.length) {
            _conditions[_conditions.length - 1].relation = 0;
        }
        this.onModelChange(this.filterConditions);
        this.onModelTouched(this.filterConditions);


        if (!_conditions.length) {
            this.conditionChange.emit(this.filterConditions);
        } else {
            this.conditionChange.emit(_conditions);
        }
    }

    expandStarFieldToAllFields() {
        const item = this.filterConditions.find(n => n.code === '*');
        if (!item) {
            return [];
        }
        const searchKeys = Array.from(new Set(item.value.trim().split(' ').filter(n => n.trim())));
        const conditions = this.fields.filter(n => n.code !== '*').map((n: SearchField, i) => {
            return searchKeys.map((s, j) => {
                return {
                    filterField: n.code,
                    value: s,
                    lbracket: '',
                    rbracket: '',
                    relation: FilterRelation.Or,
                    compare: Compare.Like,
                }
            });
        });

        let _conditions = [];
        if (conditions && conditions.length) {
            _conditions = flatten(conditions)
            _conditions[0].lbracket = '('
            _conditions[_conditions.length - 1].rbracket = ')'
            _conditions[_conditions.length - 1].relation = this.filterConditions.filter(n => n.code !=='*').length ? 1 : 0;
        }

        return _conditions;
    }

    private initDislayText(emit = true) {
        const relationStr = this.localeSer.getValue('searchbox.relation.or');
        this.displayTextList = this.filterConditions.map((t: any) => {
            const n = {...t};
            // .map(n => `â${n}â`)
            const values =  n.value.trim().split(' ').filter(n => n.trim());
            n.dislayText = n.name + 'ï¼' + values.join(` ${ relationStr } `);
            n.displayHtml = n.name + 'ï¼' + values.join(`<span class="text-muted font-italic mx-1 or">${relationStr}</span>`);
            return n;
        });

        setTimeout(() => {
            if (this.enableFloat) {
                this.setHiddenCount();
            }
        });
        this.cd.detectChanges();
        if (emit) {
            this.filterChanged();
        }
    }

    select(searchData: any) {
        const condition = this.filterConditions.find(n => n.code === searchData.code);
        if (condition) {
            condition.value = condition.value.trim() + ' ' + searchData.value;
        } else {
            this.filterConditions.push(searchData);
        }
        this.initDislayText();

        this.input.value = '';
    }

    remove(field: string) {
        this.filterConditions = this.filterConditions.filter(n => n.code !== field)
        this.initDislayText(false);

        this._emitFilterChanged();
    }

    private _emitFilterChanged() {
        if (this['REMOVE_ITEM_TIMER']) {
            clearTimeout(this['REMOVE_ITEM_TIMER']);
        }

        this['REMOVE_ITEM_TIMER'] = setTimeout(() => {
            this.filterChanged();
        }, 180);
    }

    onRemoveItem($event, item) {
        $event.stopPropagation();
        this.filterConditions = this.filterConditions.filter(n => n.code !== item.code);
        this.initDislayText();
        
    }

    clearConditions(emit = true) {
        this.input.value = '';
        this.displayTextList = [];
        this.filterConditions = [];
        this.setHiddenCount();
        if (emit) {
            this.conditionChange.emit([]);
            this.clear.emit();
        }
    }

    onSubmit($event) {
        $event.stopPropagation();
        this.input.value = '';
        this.toggleShadowBox(false);
        this._emitFilterChanged();
    }

    writeValue(obj: any): void {
        let arr = obj;
        if (!Array.isArray(obj)) {
            arr = [];
        }

        this.filterConditions = arr;
        this.initDislayText();
    }

    registerOnChange(fn: any): void {
        this.onModelChange = fn;
    }

    registerOnTouched(fn: any): void {
        this.onModelTouched = fn;
    }

    setValue(arr: Array<SearchFieldValue>, emit = true) {
        this.filterConditions = arr;
        this.initDislayText(emit);
    }

    updateShadowBoxPosition({top, left, width} = {top: null, left: null, width: null}) {
        if (!top && !left && !width) {
            const e = this.el.nativeElement.getBoundingClientRect();
            top = e.top;
            left = e.left;
            width = e.width;
        }
        this.render.setStyle(this.shadowBox.nativeElement, 'left', left + 'px');
        this.render.setStyle(this.shadowBox.nativeElement, 'top', top + 'px');
        this.render.setStyle(this.shadowBox.nativeElement, 'width', width + 'px');

        this.render.setStyle(this.shadowBox.nativeElement, 'zIndex', this.commonUtils.getFloatingLayerIndex());
    }

    private moveShadowToBody() {
        if (!this.enableFloat) {
            return;
        }
        this.updateShadowBoxPosition();
        document.body.append(this.shadowBox.nativeElement);

        this.overLayService.registerMouseEvent(this.shadowBox.nativeElement, (e) => {
            if (e.target && e.target.className.indexOf('search-box_delete-keys') > -1) {
                return;
            }

            if (e.target === this.input2.textbox.nativeElement || !this.shadowBox || this.shadowBox.nativeElement.contains(e.target) || 
            e.target['closest']('.f-search-box-panel')) {
                return;
            }

            this.toggleShadowBox(false);
        });

    }

    toggleShadowBox(show = true) {
        if (!this.enableFloat || ((!this.fields || !this.fields.length) && !this.useAnyField)) {
            return;
        }
        if (show) {
            this.updateShadowBoxPosition();
            this.render.removeStyle(this.shadowBox.nativeElement, 'display');
            this.container.nativeElement.style.visibility = 'hidden';
            this.input2.focus();
        } else {
            this.container.nativeElement.style.visibility = 'visible';
            this.render.setStyle(this.shadowBox.nativeElement, 'display', 'none');
        }
    }

    onShadowBoxClick($event: MouseEvent) {
        $event.stopPropagation();
        if (this.enableFloat) {
            this.input2.focus();
        }
    }

    onResized($event) {
        if (this.enableFloat) {
            this.updateShadowBoxPosition($event);
            setTimeout(() => {
                this.setHiddenCount();
                this.cd.detectChanges();
            });
        }
    }

    private setHiddenCount() {
        let len = 0;
        if (!this.displayTextList || !this.displayTextList.length) {
            this.hiddenItemsCount$.next(len);
            return;
        }
        const items = Array.from(this.container.nativeElement.querySelectorAll('span.search-tag-item'));
        if (items && items.length) {
            len = items.map((n: any) => {
                return n.offsetTop;
            }).filter(n => n > 20).length
        }
        this.hiddenItemsCount$.next(len);
    }

    onEsc($event) {
        this.escHandler.emit($event);
    }
}
