import { ChangeDetectorRef, Component, ElementRef, EventEmitter, HostListener, Injector, Input, OnInit, Output, ViewChild } from '@angular/core';

@Component({
    selector: 'farris-search-field-panel',
    templateUrl: './search-field-panel.component.html'
})

export class SearchFieldPanelComponent implements OnInit {

    @Input() fieldOptions;

    @ViewChild('btnSubmit') btnSubmit: ElementRef;

    @Output() filterChange = new EventEmitter();
    @Output() remove = new EventEmitter();

    conditions = null;
    elRef: ElementRef;
    constructor(private injecotr: Injector, private cd: ChangeDetectorRef) {
        this.elRef = this.injecotr.get(ElementRef, null);
    }

    ngOnInit() { }

    onSearchValueChange($event) {
        this.conditions = $event;
        this.cd.detectChanges();
    }

    onSubmitCondition($event) {
        this.fieldOptions.value = this.conditions.value;
        if (!this.fieldOptions.value.single) {
            this.fieldOptions.value.value = null;
        } else {
            delete this.fieldOptions.value.begin;
            delete this.fieldOptions.value.end;
        }
        this.filterChange.emit(this.conditions);
    }

    onRemoveSearchField($event: MouseEvent) {
        this.remove.emit(this.fieldOptions);
    }
}