import { ChangeDetectorRef, Directive, ElementRef, Injector, Optional, Renderer2 } from '@angular/core';
import { CommonUtils, OverLayHiddenService } from '@farris/ui-common';
import { LocaleService } from '@farris/ui-locale';
import { SEARCH_BOX_LOCALE } from './search-box.locale';

export class SearchBoxBase {

    public commonUtils: CommonUtils = null;
    public render: Renderer2;
    public localeSer: LocaleService
    constructor(public el: ElementRef, protected injector: Injector, @Optional() public overLayService: OverLayHiddenService, protected cd: ChangeDetectorRef) {
        this.render = this.injector.get(Renderer2);

        if (!overLayService) {
            this.overLayService = new OverLayHiddenService();
        }
        this.localeSer = this.injector.get(LocaleService, null);
        this.commonUtils = this.injector.get(CommonUtils, null);
        
        if (this.localeSer) {
            this.localeSer.setLocaleData(SEARCH_BOX_LOCALE);
        }
    }

    
}