import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'canclear'
})
export class SearchFieldCanClearPipe implements PipeTransform {

    private isNil = (val: any) => val === null  || val === undefined || val === '' || (Array.isArray(val) && !val.length);

    transform(val: any, ...args: any[]): any {
        let canClear = false;
        if (val) {
            const { value, begin, end, single } = val;

            if (!this.isNil(single)) {
                if (!single) {
                    canClear = !this.isNil(begin) || !this.isNil(end);
                } else {
                    canClear = !this.isNil(value);
                }
            } else {
                canClear = !this.isNil(value);
            }
        }

        return !canClear;
    }
}